<template>
  <div>
    <template v-if="videoSessionType == 'offline'">
      <div class="alert alert-warning" v-if="sessionUnixTime > now">
        موعد شروع کلاس فرا نرسیده است
      </div>
      <div v-else>
        <div class="alert alert-warning" v-if="!videoLink">
          فیلم ضبط شده بارگذاری نشده است
        </div>
        <div class="alert alert-success" v-else>
            کلاس برگزار شده است
          <a class="btn btn-lg btn-success">
            <i class="fas fa-play-circle"></i>
            مشاهده فیلم ضبط شده
          </a>
        </div>
      </div>
    </template>
    <template v-else>
      <div class="alert alert-warning" v-if="!videoLink">
        در انتظار برگزاری کلاس
      </div>
      <div class="alert alert-success" v-else>
          کلاس در حال برگزاری است
        <a class="btn btn-lg btn-success">
          <i class="fas fa-play-circle"></i>
          ورود به کلاس
        </a>
      </div>
    </template>
  </div>
</template>
<script>
import jmoment from "moment-jalaali";

export default {
  name: "SessionStatusMessage",
  props: {
    startDate: String,
    startTime: String,
    videoSessionType: String,
    videoLink: String,
    currentTime: String,
  },
  created() {
    this.jmoment = jmoment;
    this.sessionUnixTime = Math.floor(
      new Date(this.startDate + " " + this.startTime) / 1000
    );
    this.now =
      this.currentTime == ""
        ? Math.floor(+new Date() / 1000)
        : this.currentTime;

    console.log(this.videoLink);
  },
  data() {
    return {};
  },
};
</script>

