        </p>
        <div v-if="!videoSessionList">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else>
          <div class="row g-0">
            <img
              class="col-12 col-md-2 rounded-0 rounded-start"
              :src="API_STORAGE_URL + product.main_image_path"
            />
            <div
              class="bg-primary col-12 col-md-10 fs-2 p-2 rounded-0 rounded-end text-light"
            >
              {{ product.name }}
              <div class="fs-4 mt-3">
                جلسه
                {{ videoSession.name }}
              </div>
              <div class="fs-6 mt-3">
                روز های برگزاری:
                {{ product.days }} , ساعت برگزاری: {{ product.hour }} , تاریخ
                شروع:
                {{ product.start_date }}
              </div>
