      </div>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";
import VideoSessions from "./VideoSessions";
import SessionStatusMessage from "./SessionStatusMessage";

export default {
  name: "VideoSession",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
    this.videoSessionId = this.$route.params.videoSessionId;
    this.productId = this.$route.params.productId;
    this.getUserProduct();
    this.getProductVideos();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
    VideoSessions,
    SessionStatusMessage,
  },
  data() {
    return {
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
      product: null,
      videoSessionList: null,
      videoSession: null,
    };
  },
  methods: {
    getUserProduct() {
      this.arefApi
        .getUserProduct(this.productId)
        .then((resp) => {
          this.product = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getProductVideos() {
      this.arefApi
        .getUserProductVideoSessions(this.productId, 1, true)
        .then((resp) => {
          this.videoSessionList = resp.data;
          for (let i in this.videoSessionList) {
            if (this.videoSessionList[i].id == this.videoSessionId) {
              this.videoSession = this.videoSessionList[i];
              break;
            }
          }
          console.log(this.videoSession);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
  },
  mounted() {},
};
</script>

